:root{
  --color-light: #f8f9fa;
  --color-white: #fff;
  --color-unaccent: #808080;
  --color-medium: #e8e8e8;

  --spacing-small: 10px;
  --spacing-medium: 15px;
  --spacing-large: 30px;

  --border-radius: 3px;
  --linear-gradient: linear-gradient(to right, #0db1f1, #0ff8bb, #0db1f1);
  --box-shadow:
          0 16px 24px 2px rgba(33,37,41,.02),
          0 6px 30px 5px rgba(33,37,41,.04),
          0 8px 10px -5px rgba(33,37,41,.1);
}

*{
  box-sizing: border-box;
}

body{
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  line-height: 1.7;
  background-color: var(--color-light);
}